
@import url("../base/global.css") ;
.header__logo {
  font-size: var(--fs-500);
  color: var(--clr-primary);
  font-weight: var(--fw-extraBold);
  font-style: italic;
}

/* mobile nav */
.icon__list {
  fill: var(--clr-primary);
}
.nav__mobile__list {
  margin: var(--spacing-l2) var(--spacing-l2) var(--spacing-l6) var(--spacing-l2);
  font-size: var(--fs-400);
  color: var(--clr-base-lt);
  gap: var(--spacing-l3);
}
.nav-mobile-container {
  background-color: var(--clr-base);

  padding-bottom: var(--spacing-l);
}
.nav__link__mobile {
  padding-left: var(--spacing-m);
}

.nav__icon {
  fill: var(--clr-base-lt);
  width: 24px;
  height: 24px;
}
.header__sign {
  gap: var(--spacing-l);
}
.nav__mobile {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  overflow-y: scroll;
  background-color: var(--clr-base);
  transform: translateX(100vw);
  display: block;
  z-index: 99;
  transition: 0.4s;
}
.nav__mobile--active {
  transform: translateX(0);
}
.nav__icon__close {
  margin: var(--spacing-l) var(--spacing-l) 0 0;
  fill: var(--clr-primary);
}
.nav__close{
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

/* desktop nav */

.header__nav {
  display: none;
  font-weight: var(--fw-semibold);
}
.nav__list {
  gap: var(--spacing-l2);
  font-size: var(--fs-320);
}
.btn__accordion {
  position: relative;
}
.btn__accordion:hover .nav__hover__item {
  display: block;
  opacity: 1;
}
.nav__sublist:hover .nav__hover__item {
  display: block;
  opacity: 1;
}
.nav__hover__item {
  display: none;
  position: absolute;
  width: 17rem;
  top: 10px;
  left: -15px;
  max-height: 50rem;
  transition: all 0.4s ease-in-out;
  opacity: 0;
}
.nav__sublist {
  padding: var(--spacing-l);
  background-color: var(--clr-primary-lt);
  margin: var(--spacing-l2) 0;
  border-radius: var(--border-r);
  box-shadow: var(--shadow);
}

.sublist__item {
  margin: var(--spacing-l) 0;
}

.icon__chevron-down {
  margin-left: 0.1rem;
  fill: var(--clr-base-dk);
  width: 16px;
  height: 16px;
}

/* Media queries  */
@media all and (min-width: 800px)  {
.header__nav{
    display: block;
  }
  .btn__menu {
    display: none;
  }
  .nav__list__mobile {
    display: none;
  }
}
.overlay-nav{
    display: none;
}

